.main_container{
    border: 0px solid;
}

.login_container{
    width: 50px; 
    height: 50px; 
    margin: auto; 
    border-radius: 3px; 
}

.form_container{
    width: 50px;
    margin: auto;
}

.form_title_div{
    margin: auto; 
    display: flex;
    justify-content: center;
    align-items: center;   
    margin: auto;
}

input::placeholder {
    color: gray;
    font-size: 20px;
    margin: auto;
}

.form_title_p{
    font-weight: bold; 
    font-size: 30px; 
    display: inline-block; 
    padding-top: 10px;
}

.form_input{
    width: 800px; 
    height: 80px; 
    border: 1px solid; 
    border-radius: 13px; 
    border-color: black;
    display: flex;
    justify-content: center;
    align-items: center;   
    margin: auto;
    font-size: 20px;
}

.form_item_name{
    color: gray;
}

.form_text_alert{
    height: 10px;
}

.form_text_alert_padding{
    padding-bottom: 10px;
}

.form_submit_button{
    width: 800px; 
    height: 70px; 
    background-color: rgb(99,94,105); 
    border: 1px; 
    border-radius: 10px; 
    color: white; 
    font-size: 17px; 
    font-weight: 500;
    display: flex;
    justify-content: center;
    align-items: center;   
    margin: auto;
}

